import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import FormControlLabel from "@mui/material/FormControlLabel";

const dispatchedEvents = [];

const SrsMuiFormControlLabel = function (props, ref) {
  return (
    <FormControlLabel
      control={<slot name="control" />}
      label={props.label}
      labelPlacement={props.labelPlacement}
      disabled={props.disabled}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiFormControlLabel component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "labelPlacement",
    defaultValue: "end",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["input", "srs-mui-input", "srs-mui-switch"],
  },
  {
    name: "control",
    allowed: ["input", "srs-mui-input", "srs-mui-switch"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiFormControlLabel),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
